$colors: (
    blue50: (
        $color-blue50,
        $color-purewhite,
    ),
    blue70: (
        $color-blue70,
        $color-purewhite,
    ),
    blue90: (
        $color-blue90,
        $color-purewhite,
    ),
    brown40: (
        $color-brown40,
        $color-gray10,
    ),
    green50: (
        $color-green50,
        $color-gray10,
    ),
    orange50: (
        $color-orange50,
        $color-gray10,
    ),
    orange80: (
        $color-orange80,
        $color-purewhite,
    ),
    pink90: (
        $color-pink90,
        $color-purewhite,
    ),
    purewhite: (
        $color-purewhite,
        $color-gray10,
    ),
    purple50: (
        $color-purple50,
        $color-purewhite,
    ),
    purple80: (
        $color-purple80,
        $color-purewhite,
    ),
    red50: (
        $color-red50,
        $color-purewhite,
    ),
    teal90: (
        $color-teal90,
        $color-purewhite,
    ),
    yellow20: (
        $color-yellow20,
        $color-gray10,
    ),
);

@each $class, $color in $colors {
    $bgColor: nth($color, 1);
    $textColor: nth($color, 2);
    .background--#{$class} {
        figure {
            &:after {
                background-color: $bgColor;
            }
        }
        a,
        .card__side-image__text {
            color: $textColor;
            background-color: $bgColor;
        }
        a {
            border-bottom-color: $textColor;
            background-image: none;
            &:active,
            &:focus,
            &:hover {
                color: $textColor;
                background-color: transparent;
                background-image: none;
                border-bottom-color: $textColor;
                outline-color: $textColor;
            }
        }
    }
}

.card__side-image {
    background: $color-purewhite;
    display: flex;
    flex-direction: column;
    figure {
        picture {
            &:after {
                margin-left: 0;
                margin-top: $measure--small3;
                top: 0;
            }
        }
    }
    .card__side-image__text {
        padding: $measure--small2;
    }
    @include media('>=tablet') {
        flex-direction: row;
    }

    figure {
        align-self: stretch;
        flex-basis: 50%;
        overflow: hidden;
        padding-bottom: $measure--small4;
        position: relative;
        img {
            z-index: 1;
            position: relative;
            margin-left: 0;
        }
        &:after {
            content: '';
            height: 100%;
            left: 0;
            margin-left: $measure--small4;
            position: absolute;
            width: 100%;
            z-index: 0;
        }
    }

    .card__side-image__text {
        flex-basis: 50%;
        overflow: hidden;
        margin-top: $measure--small4;
        padding-top: $measure--small4;
    }
}
